html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

@media (max-width: 480px) {
  .player hr {
    margin-bottom: 0.938em;
    border: none;
    height: 0.063em;
    background-color: #c9c5c5; }
  .player iframe {
    width: 100%;
    height: 16.875em; }
  .player .player-text {
    padding: 1.000em; }
    .player .player-text h1 {
      font-size: 18px;
      font-weight: normal; }
    .player .player-text .text-wrapper {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: justify;
          justify-content: space-between; }
      .player .player-text .text-wrapper .channel {
        margin: 0.571em 0 0.429em 0;
        font-size: 14px; }
      .player .player-text .text-wrapper .drop {
        margin-top: 0.813em;
        width: 0;
        height: 0;
        border-left: 0.500em solid transparent;
        border-right: 0.500em solid transparent; }
      .player .player-text .text-wrapper .down {
        border-top: 0.500em solid #222;
        border-bottom: none; }
      .player .player-text .text-wrapper .up {
        border-top: none;
        border-bottom: 0.500em solid #222; }
    .player .player-text .descr {
      font-size: 14px;
      padding: 0.714em 0;
      color: #767676; }
    .player .player-text .hide {
      display: none; }
    .player .player-text .show {
      display: inline-block; } }



